<template>
  <div id='parent' :class="{parent: parentShow}">
    <my-Child @int='submitText' ></my-Child>
    我是父组件：{{ msg }}
    <br/>
    <input type="text" v-model='msg'>
  </div>
</template>

<script>
import myChild from './Child'

export default {
  name: 'parent',

  data() {
    return {
      msg: '静静等待用户输入',
      parentShow: false
    }
  },
  
  methods: {
    submitText(text) {
      this.parentShow = true;
      this.msg = text;
      this.$emit('int', this.msg);
    }
  },

  components: {
    myChild
  }
}
</script>

<style scoped>
.parent {
  color: red;
}
</style>