<template>
<div id="app" :class='{app: appShow}'>
  <my-Parent @int='changeText'></my-Parent>
  我是更高层父组件：{{ msg }}
  <br/>
  <input type="text" v-model='msg'>
</div>
</template>

<script>
import myParent from './components/Parent'

export default {
  name: 'app',

  data() {
    return {
      msg: '我也静静等待用户输入',
      appShow: false
    }
  },

  methods: {
    changeText(text) {
      this.appShow = true;
      this.msg = text;
    },
  },

  components: {
    myParent
  }
}
</script>

<style scoped>
.app {
  color: blue;
}
</style>

