<template>
  <div id="child">
    <label>
      子组件:
      <input type="text" v-model='msg'>
    </label>
    <button @click='submitText'>通知父组件</button>
  </div>
</template>

<script>
export default {
  name: 'child',

  data() {
    return {
      msg: ''
    }
  },
  
  methods: {
    submitText() {
      this.$emit('int', this.msg);
    }
  }
}
</script>

<style scoped>
#child {
  color: #000;
}
</style>
